.header-info {
  color: black;
}
.info {
  // width: 801px;
  width: 100%;
  background: #00c8ff;

  // height: 178px;

  border: 1px solid #f6ed16;
  border-radius: 6px;
}

.status {
  display: flex;

  border-bottom: 1px solid #f6ed16;

  color: #f7ee16;
  font-weight: 700;

  &-text {
    margin: 15 20 5 auto;
  }
}

.data {
  margin: 15 20;

  @media screen and (max-width: 645px) {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  &-user {
    margin-bottom: 24px;
  }

  &-info {
    display: flex;

    @media screen and (max-width: 645px) {
      max-width: 300px;

      flex-direction: column;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;
    }

    @media screen and (min-width: 646px) {
    }
    @media screen and (min-width: 1200px) {
    }
  }
}

.information {
  // justify-content: space-around;
  @media screen and (max-width: 645px) {
    display: flex;

    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  @media screen and (min-width: 646px) {
    display: flex;
  }
  @media screen and (min-width: 1200px) {
  }
  &-item {
    display: flex;
    width: 250px;

    color: #fff;
  }

  &-el {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;

    color: #000;
  }
}

.sub {
  // width: 374px;
  // max-width: 375px;

  // height: 78px;
  @media screen and (max-width: 645px) {
    margin-left: 0;
  }
  @media screen and (min-width: 646px) {
    margin-right: auto;
  }
}

.pay {
  &-item {
    display: flex;
    flex-wrap: wrap;

    &:not(:last-child) {
      margin-bottom: 12px;
    }
  }

  &-el {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;

    color: #000;
  }
}

.order {
  // height: 78px;

  @media screen and (max-width: 645px) {
    max-width: 375px;
  }

  &-item {
    display: flex;
    height: 15px;
    // overflow: hidden;
    flex-wrap: wrap;

    &:not(:last-child) {
      margin-bottom: 12px;
    }
  }

  &-el {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;

    color: #000;
  }
}

